.products {
    width: 100%;
    padding-top: 3rem;
}

.products .container {
    width: 1200px;
    margin: 0 auto;
}

.products h1 {
    font-size: 3rem;
    text-align: center;
    margin-top: 5rem;
}

.products p {
    text-align: center;
    padding: 0.5rem;
    font-weight: 600;
}

.products h2 {
    font-size: 1.7rem;
    color: white;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20%;
    width: 100%;
    background-color: rgba(0,79,179,0.5);
    text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.products .container .content {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    padding: 2rem 1rem;
    max-width: 1200px;
}

.products .card {
    border-radius: 20px;
    width: 100%;
    height: 400px;
    border-radius: 20px;
    position: relative;
    transition: 0.3s all;
  }

  .products .card:hover {
    width: 110%;
    height: 450px;
  }

  .products .card img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 20px;
  }

  .products .priceTag {
    display: flex;
    background-color: var(--primary-color);
    position: absolute;
    top: 0;
    right: 0;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 1.5rem;
    padding: 1.3rem;
    width: 35%;
  };

.products .card span {
    color: var(--primary-color);
    font-size: 1rem;
    font-weight: 700;
}

@media screen and (max-width: 900px) {
    .products .container {
        width: 100%;
    }

    .products .container .content {
        flex-direction: column;
    }

    .products .card {
        max-width: 100%;
    }
}









